@import "./../../style/theme.scss";
.body__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .text {
    object-fit: cover;
    display: flex;
    justify-content: center;
    font-family: $font;
    flex-direction: column;
    align-content: center;
    .hello__world {
      color: $dark_gray;
      font-size: large;
    }
    .pitch {
      font-size: xx-large;
    }
  }
  .image {
    display: flex;
    width: 30%;
    justify-content: center;
    align-items: center;
    .developer_image {
      width: 100%;
      filter: invert(96%);
    }
  }
}
